import React, { useState } from "react"

export default function App() {
    const [val, setValue] = useState([{
        id: 1,
        text: "something like this"
    }])
    const ClickFunc = () => {
        const newObj = {
            id: 1,
            text: "another string"
        }
        
    }
    return (
        <div>
            <ul>
                {
                    val.map((item, key) =>
                        <li key={key}>
                            <span><b>{item.id}</b></span> <br/>
                            <span>{item.text}</span>
                        </li>
                    )
                }
            </ul>
            <input type="button" value="Press" onClick={() => ClickFunc()} />
        </div>
    )
}